import React, { useEffect } from 'react';
import Header from '../../components/Header';
import { Tabs } from 'antd';
import AdminIntro from './AdminIntro';
import AdminAbout from './AdminAbout';
import { useSelector } from 'react-redux';
import AdminExperiences from './AdminExperiences';
import AdminProjects from './AdminProjects';
import AdminContact from './AdminContact';

const Admin = () => {
  const { portfolioData } = useSelector((state) => state.root);

  useEffect(() => {
    if (!localStorage.getItem('token')) {
      window.location.href = '/admin-login';
    }
  }, []);

  return (
    <div className="header">
      <Header />
      <div className="flex gap-10 items-center px-5 py-3 justify-between">
        <div className="flex gap-10 items-center">
          <h1 className="text-3xl text-primary animate__hinge">
            Portfolio Admin
          </h1>
          <div className="w-60 h-[1px] bg-gray-500"></div>
        </div>
        <h1
          className="flex underline text-primary cursor-pointer text-xl"
          onClick={() => {
            localStorage.removeItem('token');
            window.location.href = '/admin-login';
          }}
        >
          Logout
        </h1>
      </div>
      <hr />
      <div className="px-5 pb-10">
        {portfolioData && (
          <Tabs
            defaultActiveKey="1"
            items={[
              {
                label: `Intro`,
                key: '1',
                children: <AdminIntro />,
              },
              {
                label: `About`,
                key: '2',
                children: <AdminAbout />,
              },
              {
                label: `Experiences`,
                key: '3',
                children: <AdminExperiences />,
              },
              {
                label: `Projects`,
                key: '4',
                children: <AdminProjects />,
              },
              {
                label: `Contact`,
                key: '5',
                children: <AdminContact />,
              },
            ]}
          />
        )}
      </div>
    </div>
  );
};

export default Admin;
